'use client';

import React, { useState } from 'react';
import { Cliente, DetalleServicio, VentaServicio } from '../../../entities/venta';
import { formatCurrency } from '../../../lib/logistica/formatters';

type Props = {
  venta: VentaServicio;
  onClose: () => void;
  onGuardar: (ventaActualizada: VentaServicio) => void;
};

const VentaEditarModal = ({ venta, onClose, onGuardar }: Props) => {
  const [cliente, setCliente] = useState<Cliente>({
    ...venta.cliente,
    nombre: '',
    correo: '',
    telefono: '',
  });

  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [nuevoServicio, setNuevoServicio] = useState({ nombre: '', costo: 0 });
  const [descuento, setDescuento] = useState(0);
  const [error, setError] = useState('');

  const total = Math.max(0, detalles.reduce((acc, d) => acc + d.costoTotal, 0) - descuento);

  const agregarServicio = () => {
    if (!nuevoServicio.nombre || nuevoServicio.costo <= 0) return;
    const nuevoDetalle: DetalleServicio = {
      idDetalleServicio: `DET-${Date.now()}`,
      idServicio: `SVC-${Date.now()}`,
      nombreServicio: nuevoServicio.nombre,
      costoTotal: nuevoServicio.costo,
    };
    setDetalles(prev => [...prev, nuevoDetalle]);
    setNuevoServicio({ nombre: '', costo: 0 });
  };

  const editarNombreServicio = (id: string, nuevoNombre: string) => {
    setDetalles(prev =>
      prev.map(s => s.idDetalleServicio === id ? { ...s, nombreServicio: nuevoNombre } : s)
    );
  };

  const editarCostoServicio = (id: string, nuevoCosto: number) => {
    setDetalles(prev =>
      prev.map(s => s.idDetalleServicio === id ? { ...s, costoTotal: nuevoCosto } : s)
    );
  };

  const eliminarServicio = (id: string) => {
    setDetalles(prev => prev.filter(s => s.idDetalleServicio !== id));
  };

  const handleGuardar = () => {
    if (!cliente.nombre || detalles.length === 0) {
      setError('‚ö†Ô∏è Ingresa el nombre del cliente y al menos un servicio.');
      return;
    }

    const ventaActualizada: VentaServicio = {
      ...venta,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
    };

    onGuardar(ventaActualizada);
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content max-w-2xl bg-white p-6 rounded shadow-lg">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-indigo-700">‚úèÔ∏è Editar Venta</h2>
          <button onClick={onClose} className="text-red-500 hover:text-red-700 text-2xl font-bold">√ó</button>
        </div>

        {/* Cliente */}
        <section className="mb-6">
          <h3 className="font-semibold text-gray-700 mb-2">üìá Cliente</h3>
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
            <input
              className="border p-2 rounded"
              value={cliente.nombre}
              onChange={(e) => setCliente({ ...cliente, nombre: e.target.value })}
              placeholder="Nombre del cliente"
            />
            <input
              className="border p-2 rounded"
              value={cliente.correo}
              onChange={(e) => setCliente({ ...cliente, correo: e.target.value })}
              placeholder="Correo del cliente"
            />
            <input
              className="border p-2 rounded"
              value={cliente.telefono}
              onChange={(e) => setCliente({ ...cliente, telefono: e.target.value })}
              placeholder="Tel√©fono del cliente"
            />
          </div>
        </section>

        {/* Servicios seleccionados */}
        <section className="mb-6">
          <h3 className="font-semibold text-gray-700 mb-2">üõ†Ô∏è Servicios</h3>
          {detalles.length === 0 && <p className="text-sm text-gray-500">No hay servicios registrados. A√±ade uno abajo üëá</p>}
          <ul className="space-y-2">
            {detalles.map((d) => (
              <li key={d.idDetalleServicio} className="border p-3 rounded bg-gray-50 shadow-sm flex flex-col sm:flex-row justify-between items-center gap-3">
                <input
                  value={d.nombreServicio}
                  onChange={(e) => editarNombreServicio(d.idDetalleServicio, e.target.value)}
                  className="border p-2 rounded w-full sm:w-1/2"
                  placeholder="Nombre del servicio"
                />
                <input
                  type="number"
                  min={0}
                  value={d.costoTotal}
                  onChange={(e) => editarCostoServicio(d.idDetalleServicio, Number(e.target.value))}
                  className="border p-2 rounded w-32"
                  placeholder="Costo"
                />
                <button onClick={() => eliminarServicio(d.idDetalleServicio)} className="text-red-600 text-sm hover:underline">
                  Quitar
                </button>
              </li>
            ))}
          </ul>
        </section>

        {/* A√±adir nuevo servicio */}
        <section className="mb-6">
          <h3 className="font-semibold text-gray-700 mb-2">‚ûï A√±adir Servicio</h3>
          <div className="flex flex-col sm:flex-row gap-3">
            <input
              value={nuevoServicio.nombre}
              onChange={(e) => setNuevoServicio({ ...nuevoServicio, nombre: e.target.value })}
              className="flex-1 border p-2 rounded"
              placeholder="Nombre del servicio"
            />
            <input
              type="number"
              min={0}
              value={nuevoServicio.costo}
              onChange={(e) => setNuevoServicio({ ...nuevoServicio, costo: Number(e.target.value) })}
              className="w-40 border p-2 rounded"
              placeholder="Costo"
            />
            <button
              onClick={agregarServicio}
              className="bg-indigo-600 hover:bg-indigo-700 text-white px-4 py-2 rounded"
            >
              A√±adir
            </button>
          </div>
        </section>

        {/* Descuento y total */}
        <section className="mb-4 grid grid-cols-1 sm:grid-cols-2 gap-4 items-end">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Descuento (S/.)</label>
            <input
              type="number"
              value={descuento}
              min={0}
              onChange={(e) => setDescuento(Number(e.target.value))}
              className="border p-2 rounded w-full"
              placeholder="0"
            />
          </div>
          <div className="text-right text-green-700 font-bold text-lg">
            Total: {formatCurrency(total)}
          </div>
        </section>

        {error && <p className="text-sm text-red-600 mb-3">{error}</p>}

        {/* Botones */}
        <div className="flex justify-end gap-3 mt-6">
          <button onClick={onClose} className="bg-gray-200 hover:bg-gray-300 text-gray-700 px-4 py-2 rounded">‚ùå Cancelar</button>
          <button onClick={handleGuardar} className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded">üíæ Guardar</button>
        </div>
      </div>
    </div>
  );
};

export default VentaEditarModal;
