'use client';
import React, { useState, useMemo } from 'react';
import VentasTable from '../../components/ventas/VentasTable';
import VentaRegistroModal from '../../components/ventas/VentaRegistroModal';
import VentasFiltro from '../../components/ventas/VentasFiltro';
import '../../../styles/venta.css';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const GestionVentasPage = () => {
  const [modalOpen, setModalOpen] = useState(false);

  const filtrosIniciales = {
    cliente: '',
    fechaInicio: '',
    fechaFin: '',
  };

  const [filtros, setFiltros] = useState(filtrosIniciales);
  const { ventas } = useVentas();

  const limpiarFiltros = () => {
    setFiltros(filtrosIniciales);
  };

  // Filtrar ventas con l√≥gica compartida
  const ventasFiltradas = useMemo(() => {
    return ventas.filter((v) => {
      const nombreCliente = v.cliente.nombre.toLowerCase();
      const clienteOK = nombreCliente.includes(filtros.cliente.toLowerCase());

      const fechaVenta = new Date(v.fechaRegistro).getTime();
      const desde = filtros.fechaInicio ? new Date(filtros.fechaInicio).getTime() : null;
      const hasta = filtros.fechaFin ? new Date(filtros.fechaFin).getTime() : null;
      const fechaOK = (!desde || fechaVenta >= desde) && (!hasta || fechaVenta <= hasta);

      return clienteOK && fechaOK;
    });
  }, [ventas, filtros]);

  const exportarExcel = async () => {
    const XLSX = await import('xlsx');

    const data = ventasFiltradas.map((venta) => ({
      ID: venta.idVentaServicio,
      Cliente: venta.cliente.nombre,
      Correo: venta.cliente.correo,
      Tel√©fono: venta.cliente.telefono,
      Fecha: new Date(venta.fechaRegistro).toLocaleDateString(),
      Servicios: venta.detalles.length,
      Total: venta.costoTotal,
      Descuento: venta.descuento,
    }));

    const workbook = XLSX.utils.book_new();
    const worksheet = XLSX.utils.json_to_sheet(data);
    XLSX.utils.book_append_sheet(workbook, worksheet, 'Ventas');
    XLSX.writeFile(workbook, 'ventas.xlsx');
  };

  // C√°lculo de m√©tricas
  const totalVentas = ventasFiltradas.length;
  const totalIngresos = ventasFiltradas.reduce((acc, v) => acc + v.costoTotal, 0);
  const totalServicios = ventasFiltradas.reduce((acc, v) => acc + v.detalles.length, 0);
  const totalDescuentos = ventasFiltradas.reduce((acc, v) => acc + v.descuento, 0);

  return (
    <main className="ventas-layout">
      <div className="ventas-container">
        {/* Encabezado */}
        <header className="ventas-header">
          <div className="flex flex-col sm:flex-row justify-between sm:items-center gap-4">
            <div>
              <h1 className="text-2xl font-bold text-gray-800">üìä Gesti√≥n de Ventas</h1>
              <p className="text-sm text-gray-500">
                Consulta el historial y registra nuevas ventas de servicios en un solo lugar.
              </p>
            </div>
            <button className="btn-black" onClick={() => setModalOpen(true)}>
              ‚ûï Registrar Venta
            </button>
          </div>
        </header>

        {/* Filtros y botones */}
        <section className="my-6">
          <VentasFiltro filtros={filtros} onFiltroChange={setFiltros} />
          <div className="flex justify-end gap-3 mt-2">
            <button
              onClick={limpiarFiltros}
              className="bg-gray-200 hover:bg-gray-300 text-gray-800 px-4 py-2 rounded-md transition"
            >
              üßπ Limpiar filtros
            </button>
            <button
              onClick={exportarExcel}
              className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-md transition"
            >
              üì§ Exportar a Excel
            </button>
          </div>
        </section>

        {/* RESUMEN M√âTRICAS */}
        <section className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-4 mb-6">
          <div className="bg-white rounded shadow p-4 text-center">
            <p className="text-sm text-gray-500">Ventas registradas</p>
            <p className="text-2xl font-bold text-indigo-600">{totalVentas}</p>
          </div>
          <div className="bg-white rounded shadow p-4 text-center">
            <p className="text-sm text-gray-500">Total ingresos</p>
            <p className="text-2xl font-bold text-green-600">{formatCurrency(totalIngresos)}</p>
          </div>
          <div className="bg-white rounded shadow p-4 text-center">
            <p className="text-sm text-gray-500">Servicios vendidos</p>
            <p className="text-2xl font-bold text-blue-600">{totalServicios}</p>
          </div>
          <div className="bg-white rounded shadow p-4 text-center">
            <p className="text-sm text-gray-500">Total descuentos</p>
            <p className="text-2xl font-bold text-red-500">{formatCurrency(totalDescuentos)}</p>
          </div>
        </section>

        {/* Tabla de ventas */}
        <section className="ventas-section">
          <h2 className="text-lg font-semibold text-gray-700 mb-3">Historial de Ventas</h2>
          <VentasTable filtros={filtros} />
        </section>
      </div>

      {/* Modal para registrar nueva venta */}
      <VentaRegistroModal open={modalOpen} onClose={() => setModalOpen(false)} />
    </main>
  );
};

export default GestionVentasPage;
