'use client';
import { useState } from 'react';
import { VentaServicio } from '../entities/venta';
import { ventasMock } from '../mocks/ventas.mock';

export const useVentas = () => {
  // Estado inicial poblado con datos ficticios
  const [ventas, setVentas] = useState<VentaServicio[]>(ventasMock);

  // Agrega una nueva venta al final de la lista
  const registrarVenta = (nuevaVenta: VentaServicio) => {
    setVentas((prev) => [...prev, nuevaVenta]);
  };

  // Elimina una venta por su ID
  const eliminarVenta = (idVenta: string) => {
    setVentas((prev) => prev.filter((venta) => venta.idVentaServicio !== idVenta));
  };

  // Edita una venta existente comparando por ID
  const editarVenta = (ventaActualizada: VentaServicio) => {
    setVentas((prev) =>
      prev.map((venta) =>
        venta.idVentaServicio === ventaActualizada.idVentaServicio ? ventaActualizada : venta
      )
    );
  };

  return {
    ventas,
    registrarVenta,
    eliminarVenta,
    editarVenta,
  };
};
